<template>
  <scroll class="content"
          :probeType="3"
          @scroll="scroll"
          @scrollEnd="scrollEnd"
          :pullup="true"
          :scrollY="true"
          ref="scroll">
    <slot></slot>
    <div class="movie-list">
      <movie-list-item v-for="(item, index) in list"
                       :mitem="item"
                       :key="index">
      </movie-list-item>
      <loading-item v-show="isShowLoading" />
    </div>
  </scroll>
</template>

<script>
import MovieListItem from '@/components/Content/MovieListItem'
import Scroll from '@/components/Common/scroll/Scroll'
import LoadingItem from '@/components/Common/loadingItem/LoadingItem'

import { debounce } from '@/common/utils.js'

export default {
  name: 'MovieList',
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      isShowLoading: false,
    }
  },

  components: {
    MovieListItem,
    Scroll,
    LoadingItem
  },

  computed: {},

  beforeMount () { },

  mounted () {
    // 防抖
    const refresh = debounce(this.$refs.scroll.refresh, 400)

    // 所有图片加载完成后，刷新better-scroll的高度
    this.$bus.$on('itemImageLoad', () => {
      refresh()
    });
  },

  methods: {
    /**
     * better-scroll 监听滚动事件
     */
    scroll (position) {

    },

    /**
     * 上拉加载事件
     */
    scrollEnd () {
      this.showLoading()
      this.$emit('loadMoreData')
    },

    /**
     * 显示上拉加载loading
     */
    showLoading () {
      this.isShowLoading = true;
    },

    /**
     * 关闭上拉加载loading
     */
    closeLoading () {
      this.isShowLoading = false;
    },

    /**
     * 完成上拉加载(better-scroll框架)
     * 完成上拉操作后必须调用该事件
     */
    finishPullUp () {
      this.$refs.scroll.finishPullUp()
    },

    /**
     * 重新计算高度（better-scroll框架）
     * 在图片全部加载完成后需要重新计算宽度或者高度
     */
    refresh () {
      this.$refs.scroll.refresh()
    },

    // 请求数据 回调事件
    loadCallback () {
      this.finishPullUp()
      this.refresh()
      this.closeLoading()
    }
  },

  watch: {}
}
</script>
<style scoped>
.movie-list {
  background: white;
}

.content {
  height: calc(100% - 88px);
  overflow: hidden;
  position: absolute;
  top: 88px;
  left: 0;
  right: 0;
  /* bottom: 0; */
}
</style>
