<template>
   <div class="movie-list-item clear-fix">
    <router-link tag="a"
                 :to="{path:'/detail/'+mitem.id}">
      <div class="img">
        <img v-lazy="getImgSrc()" :key="getImgSrc()"
             @load="imgLoad" alt>
      </div>
      <div class="movie-info">
        <div class="movie-content">
          <h3>{{ mitem.nm }}</h3>
          <p class="wish"><span>{{ mitem.wish }}</span>想看</p>
          <p class="star line-ellipsis">{{mitem.star}}</p>
          <p class="time">{{mitem.showInfo}}</p>
        </div>
        <div class="btn-area">
          <button v-if="mitem.preShow" class="btn btn-light">想看</button>
          <button v-else class="btn">购票</button>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>

export default {
  name: 'MovieListItem',
  props: ['mitem'],
  data () {
    return {

    };
  },

  components: {},

  computed: {},

  beforeMount () { },

  mounted () { },

  methods: {
    getImgSrc(){
      let src = this.mitem.img
      if(src.indexOf('w.h') != -1){
        return src.replace('w.h','128.180');
      }
      return src;
    },
    imgLoad(){
      this.$bus.$emit('itemImageLoad')
    }
  },

  watch: {}

}

</script>
<style scoped>
.movie-list-item{
  position: relative;
  padding: 12px 15px 0;
}

.movie-list-item a{
  display: block;
  /* padding-left: 15px;
   */
}

.movie-info {
  margin-left: 80px;
  padding-right: 50px;
  min-height: 90px;
  border-bottom: 1px solid #ccc;
}

.movie-info h3 {
  font-size: 17px;
  color: var(--color-title);
  max-height: 24px;
  line-height: 24px;
  margin-bottom: 7px;
  overflow: hidden;
}

p {
  font-size: 13px;
  color: var(--color-text);
  padding-bottom: 5px;
}

.wish span {
  font-weight: bold;
  color: var(--color-text-yellow);
}

.time{
  padding-bottom: 12px;
}

.img {
  position:absolute;
  top: 10px;
  left: 16px;
  width: 64px;
  height: 90px;
  overflow: hidden;
}

.btn-area{
  position: absolute;
  right: 10px;
  top:50%;
  transform: translateY(-50%)
}


</style>